import { Component, OnInit, Input } from '@angular/core';
import { PiService } from '../pi.service';

@Component({
  selector: 'app-pin-pad',
  templateUrl: './pin-pad.component.html',
  styleUrls: ['./pin-pad.component.css'],
  providers: [PiService]
})
export class PinPadComponent implements OnInit {

  pin_numbers = [2, 3, 4, 17, 27, 22, 10, 9, 11, 0, 5, 6, 13, 19, 26, 14, 15, 18, 23, 24, 25, 8, 7, 1, 12, 16, 20, 21];
  pins: any[] = [];

  constructor(public svc: PiService) { }

  ngOnInit() {
    this.refreshPins();
  }

  private refreshPins(): void {
    this.pins = [];
    this.pin_numbers.forEach(pin => {
      this.svc.loadPinDetails(pin.toString())
        .then(res => this.parseResult(res));
    });
  }

  parseResult(res: any): void {
    this.pins.push(res);
  }

  turnOn(pin: any): void {
    this.svc.turnOnPin(pin)
      .then(() => this.refreshPins());
  }

  turnOff(pin: any): void {
    this.svc.turnOffPin(pin)
      .then(() => this.refreshPins());
  }

  handleError(error): void {
    console.log(JSON.stringify(error));
  }
}



